import Vue from 'vue'
import * as types from '@/helpers/types'
import axios from 'axios'
// import cookie from 'vue-cookie'

// *******************************************************************
// Actions
// *******************************************************************
export function commit (mutation) {
  return (context, payload) => {
    context.commit(mutation, payload)
  }
}

export function httpGetWithParameter (url, mutation = null, transformer = null) {
  return (context, payload) => {
    // const token = cookie.get('Token')
    let config = {}
    // if (token) {
    //   config = {
    //     headers: { Authorization: `Bearer ${token}` }
    //   }
    // }
    let callURL = payload ? url + payload : url
    const promise = axios.get(callURL, config)
    if (mutation) {
      promise.then((response) => {
        if (response) {
          context.commit(
            mutation,
            transformer && types.isFunction(transformer)
              ? transformer(response.data)
              : response.data
          )
        }
      })
    }
    return promise
  }
}

export function httpGet (url, payload, mutation = null, transformer = null) {
  console.log(url, mutation)
  return (context, config) => {
    // if (!config) {
    //   const token = cookie.get('Token')
    //   if (token) {
    //     config = {
    //       headers: { Authorization: `Bearer ${token}` }
    //     }
    //   }
    // }
    let params = url.match(/\{\w*\}/g)
    if (params && payload) {
      params.forEach(m => {
        url = url.replace(m, payload[m.slice(1, -1)])
      })
    } else url = url.split('?')[0]
    const promise = axios.get(url, config)
    if (mutation) {
      promise.then((response) => {
        if (response) {
          context.commit(
            mutation,
            transformer && types.isFunction(transformer)
              ? transformer(response.data)
              : response.data
          )
        }
      })
    }
    return promise
  }
}

export function httpGetByName (mutation = null, transformer = null) {
  return (context, config) => {
    const promise = axios.get(config.url, config)
    if (mutation) {
      promise.then((response) => {
        if (response) {
          context.commit(
            mutation,
            transformer && types.isFunction(transformer)
              ? transformer(response.data)
              : response.data
          )
        }
      })
    }
    return promise
  }
}

export function httpPostByName (mutation = null, transformer = null) {
  return (context, payload) => {
    const promise = axios.post(payload.url, payload.data, payload.config)
    if (mutation) {
      promise.then((response) => {
        if (response) {
          context.commit(
            mutation,
            transformer && types.isFunction(transformer)
              ? transformer(response.data)
              : response.data
                ? response.data
                : payload.data
          )
        }
      })
    }
    return promise
  }
}

export function httpPost (url, callback = null) {
  return (context, payload) => {
    const promise = axios.post(url, payload.data, payload.config)
    if (callback && types.isFunction(callback)) {
      promise.then((response) => {
        callback(context.state, response.data)
      })
    }
    return promise
  }
}

export function httpDelete (url, callback = null) {
  return (context, config) => {
    const promise = axios.delete(url, config)
    if (callback && types.isFunction(callback)) {
      promise.then((response) => {
        callback(response.data, context)
      })
    }
    return promise
  }
}

// *******************************************************************
// Getters
// *******************************************************************
export function get (prop) {
  return (state) => state[prop]
}

export function findByKey (prop, key) {
  return (state) => (value) => state[prop].find((x) => x[key] === value)
}

export function filterByKey (prop, key) {
  return (state) => (values) => {
    if (!Array.isArray(values)) values = [values]
    return state[prop].filter((x) => values.indexOf(x[key]) > -1)
  }
}

// *******************************************************************
// Mutators
// *******************************************************************
export function set (prop) {
  return (state, value) => {
    Vue.set(state, prop, value)
  }
}

export function merge (prop) {
  return (state, payload) => {
    Vue.set(
      state,
      prop,
      types.isArray(state[prop])
        ? [...state[prop], ...payload]
        : { ...state[prop], ...payload }
    )
  }
}

export function toggle (prop) {
  return (state) => {
    Vue.set(state, prop, !state[prop])
  }
}

export function pushTo (prop) {
  return (state, value) => {
    state[prop].push(value)
  }
}

export function removeFrom (prop) {
  return (state, item) => {
    const index = state[prop].indexOf(item)
    if (index >= 0) state[prop].splice(index, 1)
  }
}

export function removeById (prop) {
  return (state, item) => {
    const index = state[prop].findIndex((i) => i.Id === item.Id)
    if (index >= 0) state[prop].splice(index, 1)
  }
}

export function removeByKey (prop, key) {
  return (state, value) => {
    const index = state[prop].findIndex(
      (x) => x[key].toString() === value.toString()
    )
    if (index >= 0) state[prop].splice(index, 1)
  }
}

export function replaceByKey (prop, key) {
  return (state, item) => {
    const index = state[prop].findIndex(
      (x) => x[key].toString() === item[key].toString()
    )
    if (index >= 0) state[prop].splice(index, 1, item)
  }
}



