import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import TransportCv from '@/components/TransportCv'
import Stocks from '@/components/Stocks'
import Portfolio from '@/components/Portfolio'
import Bitcoin from '@/components/Bitcoin'
import Calendar from '@/components/Calendar'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/transport',
      name: 'Transport',
      component: TransportCv
    },
    // {
    //   path: '/stocks',
    //   name: 'Stocks',
    //   component: Stocks
    // },
    {
      path: '/bitcoin',
      name: 'Bitcoin',
      component: Bitcoin
    },
    // {
    //   path: '/calendar',
    //   name: 'Calendar',
    //   component: Calendar
    // },
    // {
    //   path: '/portfolio',
    //   name: 'Portfolio',
    //   component: Portfolio
    // }
  ]
})



