<template>
 <div>
   <div>USD: <span>{{USD.rate}}</span></div>
   <div>EUR: <span>{{EUR.rate}}</span></div>
 </div>
</template>

<script>
import '@/general-store/index'

import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'Currency',
  data () {
    return {
      name: 'Curency'
    }
  },
  computed: {
    ...mapGetters('general', [
      'currencies',
      'USD',
      'EUR'
    ])
  },
  methods: {
    ...mapActions('general', [
      'getCurrencies',
      'getUSD',
      'getEUR'
    ])
  },
  created () {
    this.getCurrencies()
    this.getUSD()
    this.getEUR()
  }
}
</script>

<style scoped>

</style>



