<template>
  <div class="cal-column" :class="{ selected: active }">
    <div class="cal-column-header">
      <span class="dayname">{{
          day.toLocaleString("default", { weekday: "long" })
        }}</span>
      <span class="daynum">{{ day.getDate() }}</span>
    </div>

    <div class="cal-column-body">
      <div class="cal-column-body-slotgrid">
        <div
          class="cal-column-body-slotgrid--lines"
          v-for="n in 24"
          :key="`${n}`"
        >
          {{ n }}:00
        </div>
      </div>
      <div class="cal-column-body-eventgrid">
        <div
          class="index"
          v-if="new Date().getDay() === day.getDay()"
          :style="{ top: scrollPercent + '%' }"
        ></div>

        <calendar-event
          v-for="(e, index) in positioning"
          :key="index"
          :data="e"
        ></calendar-event>
      </div>
    </div>
  </div>
</template>

<script>
import CalendarEvent from './CalendarEvent.vue'
import * as fn from './utils'
export default {
  name: 'calendarcolumn',
  components: {
    CalendarEvent
  },
  props: {
    precision: Number,
    day: Date,
    data: Array
  },
  data () {
    return {
      scrollPercent: 0,
      editing: false,
      active: false
    }
  },
  created () {
    let now = new Date()
    let minutes = now.getMinutes() + now.getHours() * 60
    this.scrollPercent = ((minutes - 0) / (60 * 24 - 0)) * (100 - 0) + 0
  },
  computed: {
    positioning () {
      if (!this.data) return []
      return [...this.data]
        .sort((a, b) =>
          fn.isSame(a.grid.start, b.grid.start)
            ? b.grid.dur - a.grid.dur
            : fn.diffMinutes(a.grid.start, b.grid.start)
        )
        .map(item => {
          let block = this.data.filter(
            i =>
              fn.isBefore(i.grid.start, item.grid.start) &&
              fn.isAfter(i.grid.end, item.grid.start)
          )
          if (block.length == 0) {
            item.grid['indent'] = 0
          } else {
            let maxindent = 1
            block.forEach(i => {
              if (i.grid.indent) { if (i.grid.indent >= maxindent) maxindent = i.grid.indent + 1 }
            })
            item.grid['indent'] = maxindent
          }
          let same = this.data.filter(i =>
            fn.isSame(i.grid.start, item.grid.start)
          )
          if (same.length <= 1) {
            item.grid['index'] = null
            item.grid['indexOf'] = null
          } else {
            let index = 1
            same.forEach(i => {
              if (i.grid.index) {
                if (i.grid.index == index) index++
              }
            })
            item.grid['index'] = index
            item.grid['indexOf'] = same.length
          }
          return item
        })
    }
  }
}
</script>

<style scoped>
.cal-column {
  height: inherit;
  z-index: 3;
}
.cal-column .border-left {
    border-left: 1px solid rgba(0, 0, 0, 0.1);
  }
.cal-column .cal-column-header {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  padding: 10px;
  background-color: rgba(240, 240, 240, 1);
  color: black;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  position: -webkit-sticky;
  top: 0;
  z-index: 200;
}
.cal-column .cal-column-header  .dayname {
      font-size: 16px;
      text-transform: uppercase;
    }
.cal-column .cal-column-header  .daynum {
      font-size: 20px;
    }
.cal-column .cal-column-body {
    position: relative;
    height: 100%;
}
.cal-column .cal-column-body  .cal-column-body-slotgrid,
.cal-column .cal-column-body  .cal-column-body-eventgrid,
    .cal-column-body-editgrid {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      height: 100%;
    }
/*.cal-column .cal-column-body-eventgrid {*/
