<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
import './store'
const PAGE_TIMEOUT = 30000

export default {
  name: 'App',
  data () {
    return {
      routes: [],
      selected: 0
    }
  },
  methods: {
    nextPage () {
      this.selected = this.selected + 1 > this.routes.length - 1 ? 0 : this.selected + 1
      this.$router.push(this.routes[this.selected])
      setTimeout(this.nextPage, PAGE_TIMEOUT)
    },
    getRoutes () {
      this.$router.options.routes.forEach(route => {
        this.routes.push(route.name)
      })
    }
  },
  created () {
    this.getRoutes()
    setTimeout(this.nextPage, PAGE_TIMEOUT)
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100%;
  height: 100%;
  margin: 0;
}

body {
  margin: 0;
  width: 100%;
  height: 100%;
}
</style>



